.landing-1 {

    &__title-section {
        @include container--content; }

    &__main-title {
        @include gbackground (100%); }

    &__title-wrapper-1 {
        margin-left: 15px; }

    &__title-wrapper-2 {
        box-shadow: 220px 0px 0px -14px #F5F8FF, 415px 0px 0px -14px #F5F8FF, 610px 0px 0px -14px #F5F8FF, 805px 0px 0px -14px #F5F8FF;
        padding: 15px 0;
        color: $white;
        font-size: 4.8rem;
        font-weight: 900;
        text-transform: uppercase; }

    &__title-line {
        padding-right: 15px;
        &::after {
            white-space: break-spaces;
            content: "\A"; } }

    &__title-pitch {
        display: flex;
        margin: 58px 0;
        font-size: 1.8rem; }

    // title-pitch direct children
    &__pitch-topics {
        margin-bottom: 87px; }

    &__pitch-img {
        margin: 0 -200px;
        align-self: flex-end;
        margin-bottom: 120px;
        margin-top: 142px; }

    &__pitch-price {
        margin-bottom: 108px;
        span {
            font-size: 3.6rem;
            line-height: 2.0rem; } }

    &__pitch-button {
        @include resetinteractive;
        color: $white;
        background-color: $btn;
        display: flex;
        width: 396px;
        height: 62px;
        padding: 24px;
        align-items: center;
        justify-content: space-evenly;
        margin-bottom: 156px; }
    // end of title-pitch direct children

    &__price-block {
        @include gbackground (100%);
        color: $white;
        padding: 4px 7px 5px;
        text-transform: none;
        font-weight: 900; }

    &__subtitle {
        @include subtitle(3.6rem);
        font-weight: 800; }

    &__orb-list-1 {
        @include container--content; }

    &__partners-section {
        @include container--content; }

    &__form-section {
        @include container--content; } }




