.product {
  @include container--content();

  &__wrapper {
    display: flex;
    margin-top: 118px; }

  &__gallery {
    height: 340px; }

  &__info {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 482px;
    height: 365px;
    padding: 45px 0 70px 0;
    margin-left: auto; }

  &__info-border {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1; }

  &__info-text-block {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 80%;
    z-index: 2; }


  &__code {
    @include subtitle(1.2rem);
    text-align: center;
    display: block; }

  &__title {
    @include subtitle(3.6rem);
    @include resetmargin();
    font-weight: 900;
    text-align: center; }

  &__price {
    font-size: 2.4rem;
    @include price-after(); }

  &__text {
    display: flex;
    align-items: center;
    min-height: 40px;
    margin: 30px 0 20px;
    font-size: 1.8rem;
    text-align: center; }

  &__info-buttons {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin-top: auto;
    z-index: 2; }

  &__to-cart-btn {
    font-weight: 600;
    letter-spacing: 0.5px;
    cursor: pointer; }

  &__buy-btn {
    padding: 7px 51px; }

  &__tabs {
    margin: 130px 0 80px; } }
